@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 62.5%;
}

body {
  margin: 0;
  line-height: 1;
  font-weight: 400;
  color: #555;
}

.layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 6fr;
}



.card {
  border: 2px solid #6741d9;
  border-radius: 12px;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.25);
  padding: 2rem;
}

.task-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
}

.task-item__name {
  font-size: 1.6rem;
  line-height: 1.8;
  border-bottom: 2px solid #6741d9;
}

.task-item__name > h2 {
  font-size: inherit;
}

.status {
  font-weight: 600;
}

.task-item__description {
  line-height: 1.8;
}

.task-item__description > h2 > p {
  font-weight: 400;
}

.task-item > button {
  display: inline-block;
  background-color: #6741d9;
  color: #fff;
  font-size: 1.6rem;
  padding: 1.2rem 2.8rem;
  border: 2px solid #fff;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.task-item > button:hover {
  background-color: #5f3dc4;
}

.tasks-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.body {
  padding: 3rem;
}

.new-task__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40%;

  background-color: white;
  padding: 6rem;
  border-radius: 5px;
  box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.3);
  z-index: 10;
}

.new-task__controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 5rem;
  text-align: left;
}

.new-task__control label {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-task__control input {
  font-size: 2.4rem;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 40rem;
  max-width: 100%;
  margin-bottom: 1rem;
}

.new-task__actions {
  display: flex;
  justify-content: space-between;
  padding: 0rem 4rem;
}

.new-task__actions > button {
  display: inline-block;
  background-color: #6741d9;
  color: #fff;
  font-size: 2rem;
  padding: 1.6rem 3.2rem;
  border: 2px solid #fff;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.new-task__actions > button:hover {
  background-color: #5f3dc4;
}

.search > input {
  font-size: 2.8rem;
  width: 70rem;
  padding: 1.6rem 3.2rem;
  background-color: #6741d9;
  border: none;
  border: 2px solid #fff;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  margin-right: 1.4rem;
}

.search > button {
  display: inline-block;
  background-color: #6741d9;
  color: #fff;
  font-size: 2.8rem;
  padding: 1.6rem 3.2rem;
  border: 2px solid #fff;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.search > button:hover {
  background-color: #5f3dc4;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #6741d9;
  padding: 2rem 4.8rem;
}

.header > button {
  display: inline-block;
  background-color: #6741d9;
  color: #fff;
  font-size: 2.8rem;
  padding: 1.6rem 3.2rem;
  border: 2px solid #fff;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.header > button:hover {
  background-color: #5f3dc4;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  z-index: 5;
}

.sidebar {
  background-color: orange;
}

.sidebar-actions {
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.sidebar-actions > button {
  background-color: orange;
  color: #fff;
  border: none;
  border-bottom: 4px solid #fff;
  font-size: 2.8rem;
  padding: 1.6rem 3.2rem;
  cursor: pointer;
  transition: all 0.3s;
}

.sidebar-actions > button:hover {
  background-color: rgb(227, 147, 0);
}

